@import url('https://fonts.googleapis.com/css2?family=Miriam+Libre:wght@400;700&display=swap');

* {
	margin: 0;
	padding: 0;
}

*,
*::before,
*::after {
	box-sizing: inherit;
}

html {
	box-sizing: border-box;
	font-size: 62.5%;
}

body {
	font-size: 1.6rem;
	font-family: 'Miriam Libre', sans-serif;
}

.nav-list {
	height: 10vh;
	width: 50%;
	display: flex;
	justify-content: space-around;
	align-items: center;
	margin-left: auto;
	list-style: none;
}

.nav-links {
	text-decoration: none;
	color: black;
	font-size: 1.8rem;
}

.home,
.about {
	height: 90vh;
	width: 100%;
	position: absolute;
}

.home-content,
.about-content {
	height: 100%;
	// opacity: 0;
	display: flex;
	justify-content: space-evenly;
	align-items: center;

	img {
		height: 70%;
	}

	h1 {
		font-size: 6.4rem;
		margin: 3rem 0;
		line-height: 1;
	}

	p {
		font-size: 2rem;
	}
}

.home {
	background-image: linear-gradient(
		to top right,
		#eea2a2 0%,
		#bbc1bf 19%,
		#57c6e1 42%,
		#b49fda 79%,
		#7ac5d8 100%
	);
}

.about {
	background-image: linear-gradient(to top, #cfd9df 0%, #e2ebf0 100%);
}
